import React from "react";
import "./box.css";

import { Grid, Image, Label } from "semantic-ui-react";

import image from "../../images/labyrinth.png";

function project03() {
  return (
    <div>
      <div className="project" style={{ marginTop: 60 }}>
        <Grid>
          <Grid.Column width={3}>
            <Image src={image} style={{ width: "88%" }} />
          </Grid.Column>
          <Grid.Column width={13}>
            <h1>Labyrinth</h1>
            <p>
              "Labyrinth" is a console based application (with a fancy retro
              UI). It allows you to make journal entries and save them securely
              with AES encryption. You can get the{" "}
              <a href="https://github.com/cmdrGuyson/Labyrinth" target="blank">
                GitHub
              </a>{" "}
              repository from here.
            </p>
            <Label color="pink">C++</Label>
          </Grid.Column>
        </Grid>
      </div>
    </div>
  );
}

export default project03;
