import React from "react";
import "./box.css";

import { Grid, Image, Label } from "semantic-ui-react";

import image from "../../images/myHobby.png";

function project02() {
  return (
    <div>
      <div className="project" style={{ marginTop: 60 }}>
        <Grid>
          <Grid.Column width={3}>
            <Image src={image} />
          </Grid.Column>
          <Grid.Column width={13}>
            <h1>myHobby</h1>
            <p>
              "myHobby" is an online platform for hobbyists. This react web app
              allows people enthusiastic about various hobbies to interact with
              other individuals sharing the same interests
            </p>
            <Label color="blue">React.js</Label>
            <Label color="yellow">JavaScript</Label>
            <Label color="orange">Firebase</Label>
          </Grid.Column>
        </Grid>
      </div>
    </div>
  );
}

export default project02;
