import React from "react";
import "./box.css";

import { Grid, Image, Icon, Label } from "semantic-ui-react";

import image from "../../images/aether.png";

function project01() {
  return (
    <div>
      <div className="project" style={{ marginTop: 100 }}>
        <Grid>
          <Grid.Column width={3}>
            <Image src={image} />
          </Grid.Column>
          <Grid.Column width={13}>
            <h1>
              <Icon name="discord" />
              Aether
            </h1>
            <p>
              "Aether" is a discord bot that will give you updated details on
              emergency situations happening around the world such as the recent
              covid-19 outbreak. You can get the{" "}
              <a href="https://github.com/cmdrGuyson/Aether" target="blank">
                GitHub
              </a>{" "}
              repository from here.
            </p>
            <Label color="purple">discord.js</Label>
            <Label color="yellow">JavaScript</Label>
          </Grid.Column>
        </Grid>
      </div>
    </div>
  );
}

export default project01;
